<!-- 管理课程 -->

<template>
    <div>
        <el-table :data="lessons">
            <el-table-column label="课程名称" prop="name"></el-table-column>
            <el-table-column label="简介" prop="text"></el-table-column>
            <el-table-column fixed="right" label="操作" width="600">
                <template slot-scope="scope">
                    <el-button @click="showUpdate(scope.row)" type="text" size="small">更改简介</el-button>
                    <el-button @click="deleteLesson(scope.row)" type="text" size="small">删除课程</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button @click="addLesson()" type="text" size="small">增加课程</el-button>
        <el-dialog title="新增课程" :visible.sync="dialogFormVisible" style="width: 1000px; margin: auto;">
            <el-form :model="form">
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="课程名称">
                            <el-input placeholder="课程名称" v-model="form.name" style="width: 100%;"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="课程简介">
                            <el-input type="textarea" placeholder="课程简介" v-model="form.text"
                                style="width: 100%;"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit()">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="修改简介" :visible.sync="dialogBlogVisible" style="width: 1000px; margin: auto;">
            <el-form :model="form">
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="课程简介">
                            <el-input type="textarea" placeholder="课程简介" v-model="updateForm.text"
                                style="width: 100%;"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogBlogVisible = false">取 消</el-button>
                <el-button type="primary" @click="updatetext()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import axios from 'axios';
const options = {
    inject: ['reload'],
    data() {
        return {
            lessons: '',
            form: {
                name: '',
                text: '',
            },
            dialogFormVisible: false,
            dialogBlogVisible: false,
            updateForm: {
                id: '',
                name: '',
                text: '',

            },
        }

    },
    mounted: function () {
        this.getLessons()
    },
    methods: {
        
        getLessons() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/selectLesson",
                method: 'get',
                headers: { 'Content-Type': 'application/json' }
            })
                .then(res => {
                    this.lessons = res.data;
                });

        },
        addLesson() {
            this.dialogFormVisible = true;
        },

        submit() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/addLesson",
                method: 'post',
                data: this.form,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if(res.data){
                        this.$message("增加成功");
                        this.reload();
                    }
                })
        },
        showUpdate(lesson){
            this.dialogBlogVisible = true;
            this.updateForm.id = lesson.id;
            this.updateForm.name = lesson.name;
        },
        updatetext(){
            
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/updateLesson",
                method: 'post',
                data: this.updateForm,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if(res.data){
                        this.$message("修改成功");
                        this.reload();
                    }
                })
        },
        deleteLesson(lesson){
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/deleteLesson",
                method: 'post',
                data: lesson.id,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if(res.data){
                        this.$message("删除成功");
                        this.reload();
                    }
                })
        }
    }

}
export default options;

</script>